body {
  margin: 0;
  width: 198px;
  height: 406px;
}

.stars {
  display: flex;
  margin: 16px;
}

.computer__image {
  display: flex;
  width: 169px;
  height: 134px;
  margin: 33px 22px;
}

.computer__description {
  position: center;
  margin-left: 17px;
  padding-bottom: 4px;
}

.card__product {
  position: center;
  font-size: 10px;
  line-height: 14px;
  color: #616070;
  margin: 4px 16px;
}

.stars__number {
  font-size: 10px;
  margin: 0 0 0 17px;
}

.stars__star {
  background-image: url(/src/images/star.svg);
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
}

.stars--0 .stars__star:nth-child(-n + 4) {
  background-image: url(/src/images/star-active.svg);
}

.price {
  color: #616070;
  size: 12px;
  line-height: 18px;
}

.valor__number {
  size: 16px;
  line-height: 18px;
  align-items: right;
}

.buy__button {
  padding-top: 16px;
  background-color: #00acdc;
  height: 40px;
  width: 166px;
  border-radius: 5px;
  color: #fff;
  border: none;
}
